import React, {
	useEffect,
	useState,
} from "react";


import {_monthNames } from "../stores/Utils";

import "./MonthPicker.css";
import { SvgIcon_Cmpt } from ".";


const Svg = SvgIcon_Cmpt();

const MonthPickerComponent = ({initMonth, onSelect, direction }) => {
	
	const [open, setOpen] = useState(false);
	const [show, setShow] = useState(false);


	const handleSelect = (v) => {
		let YY = (new Date()).getFullYear();
		let mm = v>10?v:"0"+v;
		let dd = mm+ "/01/"+YY;
		let dt = (new Date(dd));
		let initDate = dt.getTime();
		let lastDate = dt.setMonth(v) - 600;
		onSelect && onSelect({initDate,lastDate})
		handleClick();
	}

	const handleClick = () => {
		if(show){
			setShow(false)
			setTimeout(() => {
				setOpen(false)
			}, 200);
		}else if(!show){
			setOpen(true)
			setTimeout(() => {
				setShow(true)
			}, 50);
		}
	};

	let dir = direction || "left";


	return (
		<div className={"month_picker"}  style={{position:"relative"}} open={show} >
			<div className={"filter_month_btn _dsplFlx"} onClick={handleClick}>
				<div className="lbl date" >
						{_monthNames["en"][initMonth+1] || "Mes"}
				</div>
				
					<Svg name={"arrow_drop_down"} color={"var(--hrm_palette-blue-purple)"}/>
				
			</div>
			
			{open?
			 <div className={"OpenBoxContainer"} direction={`${dir}`}>
				 <div  className={" monthList"} style={{}}>
					{_monthNames["en"].map((mnt, wwInd)=>{
						if(!mnt){
							return null;
						}
						return (
							<div key={mnt + "_" +wwInd}  className={"filter_month_item _dsplFlx"} onClick={()=>handleSelect(wwInd)}>
								<div className="lbl tr_bar_10" >
								
								</div>
								<div className="lbl tr_bar_70" >
									{mnt}
								</div>
								<div className="lbl tr_bar_20" >
									{initMonth+1===wwInd?
									<div>
										<Svg name={"success"} color={"var(--hrm_palette-blue-purple)"}/>
									</div>
									:null}
								</div>
							</div>
						)
					})}
					</div>
			</div>
			:null}
		</div>
		
	);
};

export default MonthPickerComponent;
